<template>
  <div>
    <topnav/>
    <el-row>
     <el-col :span="7" :offset="2">
      <div class="air-title">
        <img src="../img/飞机.png" style="width: 23px;height: 23px;">&nbsp;
        <span class="air-h">国内机票</span>
      </div>
     </el-col>
     </el-row>
     <el-row>
        <el-col :span="7" :offset="2">
            <div class="air-input">
                <el-row>
                    <el-col :span="6" class="air-t">
                        <span>出发城市</span>
                        <span>到达城市</span>
                        <span>出发时间</span>
                    </el-col>
                    <el-col :span="14">
                <el-input v-model="inputgo" placeholder="请搜索出发城市"></el-input><br>
                <span v-show="show1">不能为空哦</span>
                <br>
                <el-input v-model="inputat" placeholder="请搜索到达城市"></el-input><br>
                <span v-show="show2">不能为空哦</span><br>
                <el-date-picker
                  v-model="date"
                  type="date"
                  placeholder="选择日期"
                  value-format="yyyy-MM-dd">
                </el-date-picker><br>
                <span v-show="show3">不能为空哦</span><br>
                <el-button type="primary" size="medium" @click="searchair">搜 索</el-button>
                </el-col>
                <el-col :span="4">
                     <div class="air-change"> 
                     </div>
                     <button style="position: relative;top:-44px;left:33px;" @click="change">交换</button>
                </el-col>
                </el-row>
            </div>
        </el-col>
        <el-col :span="12" :offset="1">
            <img src="../img/jp.webp" style="width: 100%; height: 350px;">
        </el-col>

     </el-row>
     <el-row >
        <el-col :offset="2" :span="20">
       <div class="air-notice">
           <el-row>
            <el-col :span="8">
           <img src="../img/保障.png"><span>100%认证</span>
            </el-col>
            <el-col :span="8">
           <img src="../img/认证 (1).png"><span>出行保证</span>
            </el-col>
            <el-col :span="8">
           <img src="../img/电话.png"><span>7×24小时服务</span>
           </el-col>
           </el-row>
       </div>
       </el-col>
     </el-row>

     <el-row>
        <el-col :span="20" :offset="2">
        <div class="air-title"><img src="../img/飞机.png" style="width: 23px;height: 23px;">&nbsp;
        <span class="air-h">特价机票</span></div>
        </el-col>
     </el-row>

  </div>
</template>

<script>
import topnav from '../components/topnav.vue'
export default {
    name:'air',
    components:{topnav},
    data(){
        return{
            inputgo:'',
            inputat:'',
            date:'',
            show1:false,
            show2:false,
            show3:false
        }
    },
    methods:{
        change(){
            let temp=this.inputat
            this.inputat=this.inputgo
            this.inputgo=temp
        },
        searchair(){
            if(this.inputgo==''){
                this.show1=!this.show1
            }
            if(this.inputat==''){
                this.show2=!this.show2
            }
            if(this.date==''){
                this.show3=!this.show3

            }
            if(this.inputgo!=''&&this.inputat!=''&&this.date!=''){
                this.$router.push({
                path:`/airdetail?city1=${this.inputgo}&city2=${this.inputat}&time=${this.date}`,
                
            })
            }
            
        }
    }

}
</script>

<style>
.air-input .el-input,.el-date-picker,.air-input .el-button{
    width: 100%; 
}
.air-input{
    border:1px solid lightgray;
    padding: 40px 15px;
    height: 350px;
}
.air-t span{
    display: block;
    margin-bottom:22px ;
    padding:9px 0 ;
    /* border: 1px solid black; */
}
.air-title{
    margin: 15px 0px;
}
.air-h{
    font-size: 24px;
    font-weight: 600;
}
.air-notice{
    height: 50px;
    background-color: rgb(220, 215, 215);
    margin-top:15px ;
}
.air-notice img{
    width: 20px;
    height: 20px;
    position: relative;
    top:3px;
}
.air-notice span{
    display: inline-block;
    line-height: 50px;
}
.air-notice .el-col{
    text-align: center;
}
.air-change{
    border: 1px solid lightgray;
    height: 60px;
    width: 50px;
    margin-top:20px;
    border-left:none;
}

</style>