<template>
  <div class="search">
    <el-row type="flex" class="search-nav">
      <span v-for="(item,index) in searchmsg" :key="item.id" @click="change(index)"
      :class="currentoption==index?'search-active':'span'">{{ item.name }}</span>
    </el-row>
    <el-row>
      <input type="text" :placeholder="searchmsg[currentoption].placeholder" class="search-input" v-model="input1">
      <button @click="searchchange()">搜 索</button>
    </el-row>
  </div>
</template>

<script>
import request from '../api/request'
export default {
  name:"search",
  data(){
    return{
      searchmsg:[
        {id:1,name:'酒店',placeholder:'搜索酒店'},
        {id:2,name:'景点',placeholder:'搜索景点'},
        {id:3,name:'机票',placeholder:'搜索机票'}
      ],

      currentoption:0,
      input1:''

    }
  },
  methods:{
    change(index){
      if(index==2){
        this.$router.push({
          path:'/air'
        })
      }
       this.currentoption=index;
       this.input1=''
    },
    searchchange(){
         if(this.currentoption==0){
          this.$router.push(
            {path:'hotalmsg',
            query:{city:this.input1,id:this.currentoption}})
         }else{
          this.$router.push({name:'placemsg',query:{city:this.input1,id:this.currentoption}})
         }

    }
  }
}
</script>

<style>
.search button{
  border: none;
  height: 40px;
  padding: 10px 10px;
  background-color: white;
  position: relative;
  top: 1px;
  cursor: pointer;
}
.search-active{
  background-color:#e8eaed ;
  color: black;
  display: inline-block;
  padding: 10px 15px;
  cursor: pointer; 

}
  .search{
    width: 600px;
    height: 150px;
    margin: 180px auto;
    border-radius: 10px;
    padding: 30px 70px;
  }
 .span{
    color: white;
    display: inline-block;
    padding: 10px 15px;
    background-color:rgb(0,0,0,0.6);
    cursor: pointer; 
    position: relative;
    top: 2px;
  
 }

   .search .search-input{
    border:1px solid white;
    outline-style: none;
    width: 400px;
    height: 40px;
    padding: 10px 10px;

  } 
  /* .search .search-input:focus{
    border: 1px solid yellow;
  } */
</style>