<template>
  <div class="login1">
    <img src="../img/用户.png"><input type="text" placeholder="用户名" v-model="uname"><br>
    <span>{{ usernotice }}</span><br>
    <img src="../img/密码 (2).png"><input type="password" placeholder="密码" v-model="upass"><br>
    <span>{{ passnotice }}</span><br>
    <button @click="islogin" style="color:white">登 录</button>
  </div>
</template>

<script>
import request from '../api/request'
export default {
  name:'login1',
  data(){
    return{
       uname:'',
       upass:'',
       usernotice:'',
       passnotice:''
    }
  },
  methods:{
    islogin(){
      // console.log(this.uname,this.upass)
        request({
          method:"post",
          url:"/isLogin.php",
          params:{
             uname:this.uname,
             upass:this.upass
          }
        }).then(res=>{
          console.log(res)
          if(res.data=='nouser'){
            this.passnotice=''
              this.usernotice='*该账户未注册'
              this.uname=''
              this.upass=''
          }else if(res.data=='error'){
            this.usernotice=''
            this.passnotice='*密码错误'
            this.upass=''
          }else{
               sessionStorage.setItem('username',this.uname);
               sessionStorage.setItem('password',this.upass);
              sessionStorage.setItem('token',res.data)
              this.$router.push({path:'/home1'});
          }
        })
    }
  }
}
</script>

<style>
   .login1 span{
    color: red;
    font-size: 10px;
   }
   .login1{
    padding-top:20px ;
   }
   .login1 input{
      outline-style: none;
      margin-top: 35px;
      padding: 8px 10px;
      width: 70%;
   }
   .login1 img{
    width: 5%;
    height: 5%;
    margin-right: 2%;
   }
   .login1 button{
    background-color: #ffa38c;
    border: none;
    padding: 10px 20px;
    width: 77%;
    margin-top:35px;
    border-radius: 5px;
   }
</style>