<template>
  <div class="pagination">
    <button @click="$emit('getcurrentpage',pageNo-1)" :disabled="pageNo==1">上一页</button>
    <button v-show="startandend.start!=1">1</button>
    <button v-show="startandend.start!=1">...</button>
    <!-- 最后一页 -->
    <button v-for="(item,index) in startandend.end" :key="index" v-show="index+1>=startandend.start" 
    :class="index+1==pageNo?'pagination-active':''"
    @click="$emit('getcurrentpage',index+1)">{{ item }}</button>
    <button v-show="startandend.end!=totalpage">...</button>
    <button v-show="startandend.end!=totalpage">{{ totalpage }}</button>
    <button @click="$emit('getcurrentpage',pageNo+1)" :disabled="pageNo==totalpage">下一页</button>
    <button>一共{{ total }}条</button>
  </div>
</template>

<script>
export default {
   name:'pagination',
   props:{
    "pageNo":Number,
    "pageSize":Number,
    "total":Number,
    "continus":Number
   },
    // ['pageNo','pageSize','total','continus'],
   data(){
    return{
    }
   },
   computed:{
    totalpage(){
        return Math.ceil(this.total/this.pageSize)
    },
    startandend(){
        let start=0
        let end=0
        if(this.continus>this.totalpage){
            start=1;
            end=this.totalpage
        }else{
            start=this.pageNo-parseInt(this.continus/2) 
            end=this.pageNo+parseInt(this.continus/2)
            if(start<=0){
                 start=1
                 end=this.continus
            }else if(end>this.totalpage){
                start=this.totalpage-this.continus+1
                end=this.totalpage

            }
           
        }  
        return {start,end}
        
    }
    
   },
   methods:{
   
   }
}
</script>

<style>
.pagination{
    text-align: center;
}
 .pagination button{
    border:none;
    padding: 10px 15px;
    border-radius: 10px;
    margin-left:20px ;
 }
 .pagination-active{
    background-color: cornflowerblue;
 }
</style>