<template>
  <div>
    <topnav/>
    <br>
    <div class="hotal-search">
    <div class="block">
    <el-input v-model="formmsg.city" placeholder="地区" style="width: 200px;" class="l"></el-input>
    <el-date-picker
      v-model="formmsg.date1"
      type="date"
      placeholder="入住日期" class="l">
    </el-date-picker>
    <el-date-picker
      v-model="formmsg.date2"
      type="date"
      placeholder="离店日期" class="l">
    </el-date-picker>
    <el-select v-model="formmsg.num" slot="prepend" placeholder="床型" class="l">
      <el-option label="不限" value="不限"></el-option>
      <el-option label="单人床" value="单人床"></el-option>
      <el-option label="双人床" value="双人床"></el-option>
      <el-option label="三人床" value="三人床"></el-option>
    </el-select>
    </div>
    <div class="block">
     <el-select v-model="formmsg.selectprice" slot="prepend" placeholder="价格区间" class="l">
        <el-option label="不限" value="0"></el-option>
      <el-option label="100以内" value="0-100"></el-option>
      <el-option label="100-200" value="100-200"></el-option>
      <el-option label="200-300" value="200-300"></el-option>
      <el-option label="300-400" value="300-400"></el-option>
      <el-option label="400-500" value="400-500"></el-option>
      <el-option label="500-1000" value="500-1000"></el-option>
    </el-select>
    <el-select v-model="formmsg.selecttype" slot="prepend" placeholder="酒店类型" class="l">
      <el-option label="不限" value="不限"></el-option>
      <el-option label="经济型" value="经济型"></el-option>
      <el-option label="商务型" value="商务型"></el-option>
      <el-option label="主题型" value="主题型"></el-option>
      <el-option label="豪华型" value="豪华型"></el-option>
    </el-select>
    <el-select v-model="formmsg.selectgrade" slot="prepend" placeholder="酒店星级" class="l">
      <el-option label="不限" value="不限"></el-option>
      <el-option label="五星" value="五星"></el-option>
      <el-option label="四星" value="四星"></el-option>
      <el-option label="三星" value="三星"></el-option>
      <el-option label="二星" value="二星"></el-option>
      <el-option label="一星" value="一星"></el-option>
    </el-select>
    
    </div>
    <el-button type="primary" size="mini" style="margin-left: 80%;margin-top: 15px;" @click="search">搜 索</el-button>
    <el-button type="danger" size="mini"  @click="clear">清 空</el-button>  
  </div>


    <div class="hotalmsg-body">
      <div class="hotalmsg-item" v-for="(item,index) in hotalmsg" :key="index">
        <el-row>
          <el-col :span="4"><img :src="require('../img/'+item.hotalphoto)"></el-col>
          <el-col :span="8" :offset="1" class="hitem"> 
            <span style="font-size:25px;font-weight: 600;cursor: pointer;" @click="detailmsg(item)">{{ item.hotalname }}</span><br>
           <span>{{item.hotaltype}}</span><br>
           <span><el-rate
            v-model="item.hotalscore"
             disabled
             show-score
            text-color="#ff9900"
            score-template="{value}">
            </el-rate></span><br>
           <img src="../img/地图-地标.png" style="width: 15px;height: 15px;position:relative;top:8px;left:16px;"><span>{{ item.hotaladdress }}</span><br>
          </el-col>
          <el-col :span="8">
          </el-col>
        </el-row>
          
      </div>
    </div>

     <pagination :pageNo="pagemsg.pageNo" :pageSize='pagemsg.pageSize' :total="pagemsg.total" 
     :continus="pagemsg.continus" @getcurrentpage="getcurrentpage"/>
  </div>
</template>

<script>
import topnav from '@/components/topnav.vue'
import pagination from '../components/pagination.vue'
import {throttle} from '../../node_modules/lodash'
import request from '../api/request'
export default {
   name:'hotalmsg',
   components:{pagination,topnav},
   data(){
    return{
        hotalmsg:[],
        id:this.$route.query.id,
        formmsg:{
           city:'',
           date1:'',
           date2:'',
           num:'',
           selectprice:'',
           selecttype:'',
           selectgrade:'',

        },
        
        pagemsg:{
          pageNo:1,  //当前页
          pageSize:5,  //一页多少条数据
          total:0,   //一共多少页
          continus:5  //出现几个按钮

        }
    }
   },
  methods:{
    detailmsg(item){
      this.$router.push({
        name:'detailhotal',
        params:{
          id:item.hotalid
        }
      })
      
    },
    search:throttle(function(){   //节流
      console.log(this.formmsg.selectprice)
      let that=this

       request({
        url:'/searchhotal.php',
        params:{
          num:this.formmsg.num,
          price:this.formmsg.selectprice,
          type:this.formmsg.selecttype,
          grade:this.formmsg.selectgrade,
        }
       }).then(function(res) {
        if(!res.data.message){
          console.log(res.data);
            res.data.map(item=>{
          item.hotalscore=parseInt(item.hotalscore)
        })
        
            that.hotalmsg=res.data
        }
        else{
          that.hotalmsg=[]
        }
            
        })
    },1000),
 
    //清空
    clear(){
      this.formmsg.date1=''
      this.formmsg.date2=''
      this.formmsg.num=''
      this.formmsg.selectprice=''
      this.formmsg.selecttype=''
      this.formmsg.selectgrade=''

    },

    //获取当前页面
    getcurrentpage(page){
      this.pagemsg.pageNo=page
      this.changepage()
      window.scrollTo(0, 0);

    },

    changepage(){
      request({
         url:`/search.php?currentpage=${this.pagemsg.pageNo}&onerows=${this.pagemsg.pageSize}&table=hotals`,
      }).then(res=>{
        console.log(res.data)
        for(let i=0;i<res.data.length-1;i++){
          res.data[i].hotalscore=parseInt(res.data[i].hotalscore);
        }
        this.pagemsg.total=res.data[res.data.length-1]
        res.data.pop();
        this.hotalmsg=res.data
      })
    }
 
  },
   created(){
    window.scrollTo(0, 0);
    console.log(this.$route.query.city)
    if(this.$route.query.city!=undefined){
      request({
        url:`/homesearch.php?city=${this.$route.query.city}&id=${this.$route.query.id}&currentpage=${this.pagemsg.pageNo}&onerows=${this.pagemsg.pageSize}&table=hotals`
      }).then(res=>{
        console.log(res.data)
        for(let i=0;i<res.data.length-1;i++){
          res.data[i].hotalscore=parseInt(res.data[i].hotalscore);
        }
        this.pagemsg.total=res.data[res.data.length-1]
        res.data.pop();
        this.hotalmsg=res.data

      })

    }else{
      this.changepage()
    }
    // request({
    //      url:`/search.php?currentpage=${this.pagemsg.pageNo}&onerows=${this.pagemsg.pageSize}&table=hotals`,
    //   }).then(res=>{
    //     console.log(res.data)
    //     for(let i=0;i<res.data.length-1;i++){
    //       res.data[i].hotalscore=parseInt(res.data[i].hotalscore);
    //     }
    //     this.pagemsg.total=res.data[res.data.length-1]
    //     res.data.pop();
    //     this.hotalmsg=res.data
    //   })
    // this.changepage()
   }

}
</script>

<style>
.page{
  text-align: center;
  margin: 10px;
}
.block span{
  line-height: 40px;
}
.hotal-search{
  border:1px solid lightgrey;
  padding:10px;
}
 .block{
    margin-top: 15px;
 }
 .l{
  margin-left:40px ;
 }
 .hotalmsg-body{
  margin-top: 15px;
 }
 .hotalmsg-body img{
  width:200px;
  height: 150px;
  float: left;
 }
 .hotalmsg-item:hover{
  border:1px solid cornflowerblue;
  box-shadow: 1px;
 }
 
 
 .hotalmsg-item{
   border:1px solid lightgray;
   border-bottom: none;
   padding: 10px;
   height: 180px;
 }

 .hotalmsg-item:last-child{
  border-bottom:1px solid lightgray;
 }
  .hitem>span{
  margin:7px 20px;
  display: inline-block;
} 


</style>