<template>
  <div class="detail-body">
    <el-row>
        <el-col :span="16">
          <div class="bread-nav">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item :to="{ path: '/hotalmsg' }">酒店</el-breadcrumb-item>
              <el-breadcrumb-item>酒店列表</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
        <div>
           <img :src="require('../img/'+hotalmsg.photo)">
        </div>
        <div>
           <span class="detail-name">{{ hotalmsg.name }}</span>
        </div>
        <div class="detail-score">
            <el-rate
            v-model="hotalmsg.score"
             disabled
             show-score
            text-color="#ff9900"
            score-template="{value}">
            </el-rate>
        </div>
        <div>
          <mapContain :address="hotalmsg.address"/>
        </div>
        <div class="detail-choose">
          <el-date-picker
            v-model="value1"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
             end-placeholder="结束日期" style="width: 45%;">
          </el-date-picker><br>
          <el-button round v-for="(item,index) in type" :key="index">{{ item}}</el-button>
          <el-button type="primary" size="mini">搜 索</el-button>
        </div>
        <div>
             <div v-for="(item,index) in detailmsg" :key="index" class="detail-item">
              <el-row>
                <el-col :span="8">
                  <span><img :src="require('../img/'+item.roomphoto)"></span>
                </el-col>
                <el-col :span="16">
                  <span class="item-roomname">{{ item.roomtype }}</span>
                  <span>{{ item.roomnum }}人入住</span>
                  <span class="item-notice">
                  <img src="../img/对勾.png" style="width: 13px;height: 13px;margin-right: 2px;">入住当天18：00可以免费取消</span>
                  <span class="item-notice">
                  <img src="../img/对勾.png" style="width: 13px;height: 13px;margin-right: 2px;">极速退款</span>
                  <span>{{ item.roomother }}</span>
                  <span class="item-money">￥{{ item.roomprice }}</span>
                  <el-button type="primary" style="position: absolute;top:80%;left:80%;margin-bottom:0px;" @click="addorder(item.roomprice,item.id)">订 购</el-button>
                </el-col>
              </el-row>
                  
             </div>
        </div>
       </el-col>
    </el-row>
  </div>
</template>

<script>
import request from '../api/request'
import mapContain from '../components/mapContain.vue'
export default {
  name:'detailhotal',
  components:{mapContain},
  data(){
    return{
        hotalmsg:{
            photo:'',
            score:0,
            name:'',
            address:''

        },
        detailmsg:[],
        type:[],
        value1:''
    }
  },
  methods:{
    addorder(price,id){
          this.$confirm('是否下单？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          request({
         url:`/addorder.php?type=hotal&price=${price}&uid=${sessionStorage.getItem('username')}&id=${id}`,
      }).then(res=>{
         console.log(res.data)
         this.$message({
            type: 'success',
            message: '下单成功'
          });
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      }
    
  },
  created(){
    window.scrollTo(0,0)
    let id=this.$route.params.id;
    request({
         url:`/getdetail.php?id=${id}`,
      }).then(res=>{
        this.detailmsg=res.data
        this.hotalmsg.photo=res.data[0].hotalphoto
        this.hotalmsg.name=res.data[0].hotalname
        this.hotalmsg.score=parseInt(res.data[0].hotalscore)
        this.hotalmsg.address=res.data[0].hotaladdress
        let type=res.data.map(item=>{
          return item.type
        })
        this.type=[...new Set(type)]
        console.log(res.data)
      })
  }
}
</script>

<style>
.bread-nav{
  padding-top: 10px;
}
.detail-choose{
  border:1px solid lightgrey;
  padding: 10px;
  border-radius: 10px;
}
.item-money{
  color: red;
  font-size: 25px;
  font-weight: 600;
  position: absolute;
  top:80%;
  left:65%;
}
.item-notice{
  color: #1296db;
}
.detail-item:hover{
  border-color: #1296db;
}
.detail-item span{
  display: block;
  margin-bottom: 8px;
}
.item-roomname{
  font-size: 20px;
  font-weight: 600;
}
   .detail-item img{
    width: 200px;
    height: 200px;
    border-radius: 10px;
    
   }
   .detail-item{
    border:1px solid lightgray;
    padding: 20px;
    border-radius: 10px;
    cursor: pointer;
   }
   .detail-name{
    font-size: 40px;
    font-weight: 600;
   }
   .detail-body{
    margin: 5px;
   }
   .detail-body div{
    margin-bottom: 20px;
   }
   .detail-score{
    padding-top: 20px;
    height: 60px;
    background-color: #f5f4f0;
    border-radius: 10px;
    
   }
</style>