<template>
  <div id="container"></div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader'
import maps from '../api/map'
window._AMapSecurityConfig = {
  securityJsCode: 'f057abc990551ea29580ba15e87e032b'
}
export default {
    name:'mapContain',
    components:{maps},
    data(){
        return{
           map:null,
           key:'81883048083422e2b1547a6eeaf09475',
           position:[]
        }
        
    },
    props:['address'],
    methods:{
        getplace(){ //将地址转化为经纬度
            console.log(this.address)
           maps({
              url:`/v3/geocode/geo?address=${this.address}&output=json&key=${this.key}`,
           }).then(res=>{
               this.position.push(res.data.geocodes[0].location)
            })
        },
        initMap() {
      AMapLoader.load({
        key: '756a7bf4659ad699a7f819fb665f1103', // 申请好的Web端开发者Key，首次调用 load 时必填
        version: '2.0', // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: [''] // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      })
        .then(AMap => {
            let that=this
          this.map = new AMap.Map('container', {
            //设置地图容器id
            viewMode: '3D', //是否为3D地图模式
            zoom: 13, //初始化地图级别
            center: that.aposition //初始化地图中心点位置
          })
          
        })
        .catch(e => {
          console.log(e)
        })
    }
    },
    mounted() {
        this.getplace()
    //DOM初始化完成进行地图初始化
    this.initMap()
    }

}
</script>

<style>
  #container{
    height: 300px;
  }
</style>