<template>
  <div class="filghtitem-item">
              <el-row class="airdetail-item" >
                <el-col :span="6">
                <span style="font-size: 20px;">{{ item.aair }}</span><br>
                <span style="">{{ item.aid }}</span>  
                </el-col>
                <el-col :span="4">
                <span>{{ item.astarttime }}</span><br>
                <span>{{ item.astartplace }}</span>
                </el-col>
                <el-col :span="4">
                <img src="../img/屏幕截图 2023-04-05 232807.png" style="height: 20px;width:100px">  
                </el-col>
                <el-col :span="4">
                <span>{{ item.aendtime }}</span> <br>
                <span>{{ item.aendplace }}</span> 
                </el-col>
                <el-col :span="4">
                <span class="money">￥{{ item.aprice }}</span> 
                </el-col>
                <el-col :span="2">
                <el-button type="primary" size="mini" @click="changeshow" >订购</el-button> 
                </el-col>
              </el-row>
              <el-row class="fightitems" v-show="ishow">
                 <el-row v-for="items in filghts" :key="items.id" style="padding:25px 20px;background-color:lightgray;" >
                    <el-col :span="4" :offset="1">
                      机票
                    </el-col>
                    <el-col :span="12">
                      {{items.type}}&nbsp;&nbsp;|&nbsp;&nbsp;{{ items.place }}
                    </el-col>
                    <el-col :span="4" style="color:red;font-weight: 600;">
                      ￥{{items.price}}
                    </el-col>
                    <el-col :span="2">
                      <el-button type="warning" size="mini" @click="dialogFormVisible = true">选定</el-button><br>
                      <span>剩余：</span><span>{{ items.num }}</span>
                    </el-col>
                     
                    <el-dialog title="添加信息" :visible.sync="dialogFormVisible">
            <div style="border: 1px solid lightgray;padding: 15px;">用户信息：
              <el-button size="mini" v-for="item,index in person" :key="index">{{item.name}}</el-button>
            </div>
                <el-table
              ref="multipleTable"
              :data="tableData"
              tooltip-effect="dark"
              style="width: 100%"
              @selection-change="handleSelectionChange">
              <el-table-column
                type="selection"
                width="55">
              </el-table-column>
              <el-table-column
              prop="status"
                label="状态"
                width="100"
                v-show="false">
              </el-table-column>
              <el-table-column
                 prop="name"
                label="姓名"
                width="100">
              </el-table-column>
              <el-table-column
                prop="id"
                label="身份证"
                width="200">
              </el-table-column>
              <el-table-column
                label="操作"
                show-overflow-tooltip>
                <template slot-scope="scope">
                  <el-button size="mini" type="primary" @click="addperson(scope.row)" 
                  :disabled="scope.row.status==0">添加</el-button>
                </template>
              </el-table-column>
              </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addorder(items.place,items.id)">确 定</el-button>
          </div>
             </el-dialog>

                 </el-row>
              </el-row>

 
  </div>
</template>

<script>
import mixin from '@/mixin/mixin'
import request from '../api/request'
export default {
    name:'filghtitem',
    mixins:[mixin],
    props:['item','time'],
    data(){
        return{
            filghts:[],
            ishow:false,
            dialogFormVisible:false,
            person:[],
            first:['1A','1C','1L','1J','2A','2C','2L','2J']
        }
    },
    methods:{
      //获取机票列表
        getfightitem(){
            request({
         url:`/searchairdetail.php?id=${this.item.aid}`,
      }).then(res=>{
        // console.log(res.data)
        this.filghts=res.data
       
      },err=>{
        console.log(err)
      })
        },

        changeshow(){
          this.ishow=!this.ishow
        },
        addorder(price,id){
          this.person.map(item=>{
            item.sit=this.first[Math.floor(Math.random()*8)]
          })
          this.$router.push({
            path:'/airordermsg',
            query: {
              person:this.person,
              price:price,
              id:id,
              time:this.time
             }

          })
      },
      handleSelectionChange(){

      },
      addperson(row){
          row.status=0
          this.person.push(row)
          console.log(row)
          
      }


      
    },
    created(){
      this.getairperson()
      console.log(this.tableData)
      this.getfightitem()
        // console.log(this.item.aid)
    }

}
</script>

<style>
.fightitems{
  border: 1px solid lightgray;
  margin-top:5px ;
   
}
</style>