<template>
  <div>
    <myroder :type="type" :getdata="placedata"/>
  </div>
</template>

<script>
import request from '../api/request'
import myroder from '@/components/myroder.vue';
export default {
    name:'placeorder',
    components:{myroder},
    data(){
        return{
            type:'place',
            placedata:[],
            buttonbefore:['取消订单','去使用'],
            buttonafter:['评价','删除订单']
        }
    },
    methods:{
        getdata(){
            request({
            url:`/getorders.php?type=${this.type}&uid=${sessionStorage.getItem('username')}`,
          }).then(res=>{
           console.log(res.data)
           res.data.map(item=>{
            this.placedata.push({
                oid:item.oid,
                oname:item.placename,
                ostatus:item.ostatus,
                ooid:item.pid,
                ophoto:item.placephoto,
                oother:[item.type,item.placeother,'￥'+item.placeprice],
                buttonbefore:this.buttonbefore,
                buttonafter:this.buttonafter
            })
           })
      })

        }

    },
    created(){
        this.getdata()
    }

}
</script>

