<template>
  <div class="content">
      <div>
      <span style="margin-left:25% ;">—————&nbsp;&nbsp;</span>
      <span style="font-size: 40px;font-family:StKaiti;">热门景点</span>
      <span>&nbsp;&nbsp;—————</span>
      <span style="margin-left:20% ;"><router-link to="/placemsg">查看更多<img src="../img/按钮-箭头向右.png" class="p"></router-link></span>
      </div>
      <br>
      <div style="display: flex;justify-content: space-between;flex-wrap: wrap;">
      <div class="content-item" v-for="item in placeList" :key="item.pid" @click="placedetail(item.pid)">
         <img :src="require('../img/'+item.placephoto)">  
         <div class="item" @mouseenter="over($event)" @mouseleave="leave($event)">
            <span style="font-size: 30px;font-weight: 700;">{{item.placename}}</span><br>
            <span style="color:#ffa300;font-size:20px;">￥{{item.placeprice}}</span><br><br><br>
            <span class="other">{{item.placeother}}</span>
         </div>
      </div>
      </div>
   </div>
</template>

<script>
import request from "../api/request";
export default {
  name:"place",
  data(){
   return{
      placeList:[],
      url:"img/home.webp"
   }
  },
  methods:{
      placedetail(id){
         this.$router.push({
            path:`/detailplace/${id}`
         })


      },
      over($event){
         $event.currentTarget.className='item2';
        console.log($event.currentTarget.className)
      },
      leave($event){
         $event.currentTarget.className='item';
      }


  },
  created(){
      request({
         url:"/getplacelist.php",
      }).then(res=>{
         this.placeList=res.data;
         // console.log(res.data)
      })
  }
}


</script>

<style>
   h1 span{
    color: lightgrey;
   }
   .content .p{
      position: relative;
      top:5px;
      width: 25px;
      height: 25px;
   }
   .content-item img{
      width: 100%;
      height: 100%;
      border-radius: 10px;
   }
   .content a:hover{
      color:#ffa300;
   }
   .content{
    padding-top: 20px;
    width: 70%;
    margin: 0 auto;
   }
   .content-item{
    width: 32%;
    height: 300px;
    margin-top: 20px;
    position: relative;
    cursor: pointer;
   }
   .item{
      position: absolute;
      top:60%;
      width: 100%;
      height: 40%;
      padding: 30px 30px;
      color: white;
      background-color: rgb(0,0,0,0.5);
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
      
   }
   .item2{
      position: absolute;
      top:0;
      width: 100%;
      height: 100%;
      padding: 30px 30px;
      background-color: rgb(0,0,0,0.5);
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
   }
   .item2 span{
      color:white;
      display: block;
   }
   .item .other{
      display: none;

   }
   /* .content-item:hover .item{
      animation-name: item;
      animation-duration: 1s;
   } */
   /* .item span{
      color:white;
      display: block;
   } */

</style>