import request from '../api/request'
const mixin={
    data(){
        return{
            tableData:[],
        }

    },
    methods:{
        getairperson(){
            request({
             url:`/getairperson.php?userid=${sessionStorage.getItem('username')}`,
          }).then(res=>{
            
            // this.usermsg=res.data
            this.tableData=[]
            res.data.map(item=>{         
              this.tableData.push({
                id:item.mpersonid.replace(/(\d{4})\d{11}(\d{3})/,'$1***********$2'),
                name:item.mname,
                phone:item.mphone,
                status:1
              })
    
            })
        //   console.log(this.tableData)

          })
          },
    }

}
export default mixin;