import Login from "../pages/login";
import VueRouter from "vue-router";
import login1 from "../pages/login1"
import login2 from "../pages/login2"
import home1 from "../pages/home1"
import tourmsg from "../pages/tourmsg"
import placemsg from '../pages/placemsg'
import hotalmsg from '../pages/hotalmsg'
import detailhotal from '../pages/detailhotal'
import usermsg from '../pages/usermsg'
import userinfo from '../pages/userinfo'
import userpass from '../pages/userpass'
import air from '../pages/air'
import airdetail from '../pages/airdetail'
import detailplace from '../pages/detailplace'
import hotalorder from '../pages/hotalorder'
import placeorder from '../pages/placeorder'
import airorder from '../pages/airorder'
import airordermsg from '../pages/airordermsg'
const router = new VueRouter({
   routes:[
            {
             path:'/login',
             component:Login,
             children:[
              {
                 path:'login1',
                 component:login1
              },
              {
                 path:'login2',
                 component:login2
              }
             ]

            },
            // {
            //   path:'/',
            //   component:Home,
            // },
            {
               path:'/home1',
               component:home1,
            },
            {
               path:'/tourmsg',
               component:tourmsg,
              },
              {
               path:'/air',
               component:air,
              },
              {
               path:'/airdetail',
               component:airdetail,
              },
              {
               path:'/airordermsg',
               component:airordermsg,
              },
              {
               name:'placemsg',
               path:'/placemsg',
               component:placemsg,
               props:true
              },
              {
               name:'detailplace',
               path:'/detailplace/:id',
               component:detailplace
              },
              {
               path:'/hotalmsg',
               component:hotalmsg,
              },
              {
               name:'detailhotal',
               path:'/detailhotal/:id',
               component:detailhotal
              },
              {
               name:'usermsg',
               path:'/usermsg',
               component:usermsg,
               children:[
                  {
                     path:'userinfo',
                     name:'userinfo',
                     component:userinfo
                  },
                  {
                     path:'userpass',
                     name:'userpass',
                     component:userpass
                  },
                  {
                     path:'hotalorder',
                     name:'hotalorder',
                     component:hotalorder
                  },
                  {
                     path:'placeorder',
                     name:'placeorder',
                     component:placeorder
                  },
                  {
                     path:'airorder',
                     name:'airorder',
                     component:airorder
                  },
               ]
              },
              {
               //主页重定向
               path:'*',
               redirect:'/home1',
              }
            // {
            //    path:'/layout',
            //   component:layout,
            //   children:[{
            //    path:'home1',
            //    component:home1,
            //   },{
            //    path:'tourmsg',
            //    component:tourmsg,
            //   },{
            //    name:'placemsg',
            //    path:'placemsg',
            //    component:placemsg,
            //    props:true
            //   },{
            //    path:'hotalmsg',
            //    component:hotalmsg,
            //   }
            //   ]
            // }
        ]


})


export default router