<template>
  <div>
    <div class="detailplace-top">
        <el-row>
            <el-col :span="11">
            <img :src="require('../img/'+placemsg[0].placephoto)" style="height: 350px;">
            </el-col>
            <el-col :span="12" :offset="1" class="detailplace-content">
            <h1>{{ placemsg[0].placename }}</h1>
            <span>{{ placemsg[0].placeother }}</span>
            <span>位置：<img src="../img/地图-地标.png" style="width: 20px;height: 20px;">&nbsp;&nbsp;{{ placemsg[0].placeaddress }}</span>
            <span>用户点评：<b style="color: red;font-size: 20px;">{{ placemsg[0].placescore }}</b>&nbsp;&nbsp;/&nbsp;&nbsp;10</span>
            <span>销量：{{ placemsg[0].psellnum }}</span>
            </el-col>
        </el-row>
    </div>
    <div class="detailplace-price">
        <h1>门票</h1>
        <br>
        <div v-for="item,index in placemsg" :key="item.id" class="detailplace-item">
           <h3>{{ item.type }}</h3>
           <p v-for="items,indexs in notice[index].no" :key="indexs">{{ items }}</p>
           <div style="position: absolute;top:50%;left: 90%;">
            <span style="color: red;font-weight: 600;">￥{{ item.price }}</span><br>
            <el-button type="danger" size="mini" @click="addorder(item.price,item.id)">订购</el-button>
            </div>
       </div>
    </div>
  </div>
</template>

<script>
import request from '../api/request'
export default {
    name:'detailplace',
    data(){
        return{
            placemsg:[],
            notice:[]
        }

    },
    methods:{
        addorder(price,id){
          this.$confirm('是否下单？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          request({
         url:`/addorder.php?type=place&price=${price}&uid=${sessionStorage.getItem('username')}&id=${id}`,
      }).then(res=>{
         console.log(res.data)
         this.$message({
            type: 'success',
            message: '下单成功'
          });
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      }

    },
    created(){
        console.log(this.$route.params.id)
        request({
         url:`/getplacedetail.php?id=${this.$route.params.id}`,
      }).then(res=>{
        console.log(res.data)
        res.data.map(item=>{
            let arr=item.notice.split('；')
            this.notice.push({id:item.id,no:arr})

        })
        console.log(this.notice)
        this.placemsg=res.data
      })
    }


}
</script>

<style>
.detailplace-item{
    border:1px solid lightgray;
    border-radius: 10px;
    margin: 10px 0;
    padding: 20px;
    position: relative;
}
.detailplace-item:hover{
    border-color: rgb(59, 171, 232);
}
.detailplace-item p{
    margin: 10px 0;
}
 .detailplace-top,.detailplace-price{
    margin: 20px;
    padding: 20px;
    border: 1px solid lightgray;
 }
 .detailplace-content h1{
    margin-bottom: 50px;
 }
 .detailplace-content span{
    display: block;
    margin-top:10px ;
 }
</style>