<template>
  <div>
    <myroder :type="type" :getdata="hotaldata"/>
  </div>
</template>

<script>
import request from '../api/request'
import myroder from '@/components/myroder.vue';
export default {
    name:'hotalorder',
    components:{myroder},
    data(){
        return{
            hotaldata:[],
            type:'hotal',
            buttonbefore:['取消预定','再次预定','点击入住'],
            buttonafter:['评价','删除订单']
        }

    },
    methods:{
        getdata(){
            request({
            url:`/getorders.php?type=${this.type}&uid=${sessionStorage.getItem('username')}`,
          }).then(res=>{
           console.log(res.data)
           res.data.map(item=>{
            this.hotaldata.push({
                oid:item.oid,
                ooid:item.hotalid,
                oname:item.hotalname,
                ostatus:item.ostatus,
                ophoto:item.roomphoto,
                oother:[item.roomtype,item.roomother,item.otime,'￥'+item.roomprice],
                buttonbefore:this.buttonbefore,
                buttonafter:this.buttonafter
            })
           })
      })

        }
    },
    created(){
        this.getdata()
    }
}
</script>

