<template>
  <div class="userinfo">
    <el-row>
      <el-col :span="4" style="text-align: center;">
        <img src="../img/头像 女孩.png" style="width: 120px;height:120px;"><br><br>
    <el-button type="primary" size="mini" @click="showPic">上传头像</el-button>
    <el-dialog title="上传头像" :visible.sync="dialogPicVisible">
         <div class="upload-pic">
          <span>+</span>
          <!-- <img src=""> -->
         </div>
         <input type="file" class="upload-input" >
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogPicVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogPicVisible = false">确 定</el-button>
          </div>
    </el-dialog>
      </el-col>
      <el-col :span="18" class="userinfo-title" :offset="1">
        <p class='userinfo-air'><span style="font-weight: 800;">个人基本信息</span>
          <el-button type="primary" size="mini">修改信息</el-button></p>
        <hr>
        <p>昵称：{{ usermsg[0].uname }}</p>
        <p>性别：{{usermsg[0].usex }}</p>
        <p>电话：{{ usermsg[0].uphone}}</p><br>
        <p class='userinfo-air'><span style="font-weight: 800;">机票个人信息管理</span>
          <el-button size="mini" @click="showdialog" style=" padding: 4px 10px;font-size: 20px;">+</el-button></p><hr>

          <el-dialog title="添加信息" :visible.sync="dialogFormVisible">
          <el-form :model="form">
             <el-form-item label="姓名" :label-width="formLabelWidth">
             <el-input v-model="form.name" autocomplete="off" @blur="form.name==''?isshow1=true:isshow1=false"></el-input><span class="userinfo-notice" v-show="isshow1" >*不能为空哦</span>
             </el-form-item>
             <el-form-item label="身份证" :label-width="formLabelWidth">
             <el-input v-model="form.id" autocomplete="off" @blur="form.id==''?isshow2=true:isshow2=false"></el-input><span class="userinfo-notice" v-show="isshow2"> *不能为空哦</span>
             </el-form-item>
             <el-form-item label="电话" :label-width="formLabelWidth">
             <el-input v-model="form.phone" autocomplete="off" @blur="form.phone==''?isshow3=true:isshow3=false"></el-input><span class="userinfo-notice" v-show="isshow3"> *不能为空哦</span>
             </el-form-item>
           </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addperson">确 定</el-button>
          </div>
          </el-dialog>



        
          <el-table
    ref="multipleTable"
    :data="tableData"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
    prop="name"
      label="姓名"
      width="80">
    </el-table-column>
    <el-table-column
      prop="id"
      label="身份证"
      width="160"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="phone"
      label="电话"
      width="100"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      label="操作"
      show-overflow-tooltip>
      <template slot-scope="scope">
      <el-button size="mini" type="primary">修改</el-button>
      <el-button size="mini" type="danger" @click="delmsg(scope.row)">删除</el-button>
      </template>
    </el-table-column>
          </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import request from '../api/request'
import qs from 'qs'
import mixin from '../mixin/mixin'
export default {
    name:'userinfo',
    mixins:[mixin],
    data(){
        return{
            userid:'',
            usermsg:[],
            // tableData:[],
        dialogFormVisible: false,
        dialogPicVisible:false,
        form: {
          name: '',
          id:'',
          phone:''
        },
        formLabelWidth: '120px',
        isshow1:false,
        isshow2:false,
        isshow3:false,

        }
    },
    created(){
        this.userid=sessionStorage.getItem('username')
        request({
         url:`/getuser.php?userid=${this.userid}`,
      }).then(res=>{
        console.log(res.data)
        this.usermsg=res.data
      })
      this.getairperson()
        
    },
    methods:{

      // getairperson(){
      //   request({
      //    url:`/getairperson.php?userid=${sessionStorage.getItem('username')}`,
      // }).then(res=>{
      //   console.log(res.data)
      //   // this.usermsg=res.data
      //   this.tableData=[]
      //   res.data.map(item=>{         
      //     this.tableData.push({
      //       id:item.mpersonid.replace(/(\d{4})\d{11}(\d{3})/,'$1***********$2'),
      //       name:item.mname,
      //       phone:item.mphone
      //     })

      //   })
      // })
      // },
      delmsg(msg){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          request({
         url:`/delperson.php?userid=${sessionStorage.getItem('username')}&phone=${msg.phone}`,
         }).then(res=>{
          if(res.data.message=='success'){
            this.$message({
          message: '删除成功',
          type: 'success'
          });            
          }
          this.getairperson()
          
      })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      },

      handleSelectionChange(){
        
      },
      showdialog(){
        this.dialogFormVisible=true
      },
      showPic(){
        this.dialogPicVisible=true
      },
      addperson(){
        if(this.form.id!=''&&this.form.name!=''&&this.form.phone!=''){
          
        let data=qs.stringify({
           uid:sessionStorage.getItem('username'),
           name:this.form.name,
           id:this.form.id,
           phone:this.form.phone
        })

        request.post('/addpersonmsg.php',data).then
        (res=>{
          if(res.data.message=='success'){
            this.$message({
          message: '添加成功',
          type: 'success'
        });
         this.dialogFormVisible=false
         this.getairperson()
          }
        
      })
        }else{
          this.$message({
          message: '请填写全部',
          type: 'warning'
        });
        }

        

      }
    }

}
</script>

<style>
  .userinfo{
    padding: 40px;
  }
  .userinfo-title{
    padding: 10px;

  }
  .userinfo-title p{
    margin-bottom:15px ;
    font-size: 20px;
  }
  .userinfo-air{
    position: relative;
  }
  .userinfo-air .el-button{
    position: absolute;
    right:10px;
  }
  .el-dialog .el-input{
    width: 70%;
  }
  .upload{
    font-size:12px;
    margin-left: -40px;
    overflow:hidden;
    position:absolute;
    background-color: aqua;
    padding: 10px;
    border-radius:5px ;
    color: white;
  }
.file{
    position:absolute;
    z-index:100;
    margin-left:-100px;
    font-size:30px;
    opacity:0;
    filter:alpha(opacity=0);
    margin-top:-5px;
}
.upload-pic{
  width: 150px;
  height: 150px;
  border: 1px dotted black;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 50%;
  font-size: 30px;
}
.upload-input{
  position: absolute;
  top:40%;
  left:2px;
  font-size:40px ;
  /* opacity: 0; */
  overflow: hidden;
}
.userinfo-notice{
  color: red;
}


</style>