<template>
  <div class="airdetail">
    <topnav/>
    <el-row :gutter="15" class="airdetail-search">
        <el-col :span="17" :offset="1">
            <div style="display: flex;justify-content:space-between;">
            <span>{{ this.$route.query.city1 }}-{{ this.$route.query.city2 }}</span> 
            <el-select v-model="airport" placeholder="起飞机场" >
               <el-option
               v-for="item in airports"
               :key="item.value"
               :label="item.label"
               :value="item.value">
               </el-option>
            </el-select>
            <el-time-select
           v-model="starttime"
            :picker-options="{
            start: time.start,
            step: '00:15',
            end: time.end
            }"
            placeholder="起飞时间" >
           </el-time-select>
            <el-select v-model="airplace" placeholder="航空公司" >
               <el-option
               v-for="item in airplaces"
               :key="item.value"
               :label="item.label"
               :value="item.value">
               </el-option>
            </el-select>
            <el-select v-model="time" placeholder="日期">
               <el-option
               v-for="item in airtime"
               :key="item.value"
               :label="item.label"
               :value="item.value">
               </el-option>
            </el-select>
            </div>
               <el-row class="airdetail-but">
                  <el-button type="primary" size="mini" @click="getdata">撤销</el-button>
                  <el-button type="primary" size="mini" @click="seachdata">查询</el-button>
               </el-row>
            <div class="airdetail-title">
              <el-row>
                <el-col :span="5" :offset="1">航空信息</el-col>
                <el-col :span="7" :offset="1">起飞时间</el-col>
                <el-col :span="4">到达时间</el-col>
                <el-col :span="3">价格</el-col>
              </el-row>
            </div> 
            <div v-for="(item,index) in airmsg" :key="index" >
              <filghtitem :item="item" :time="time"/>
            </div>
            <div v-show="airmsg.length==0">暂无航班信息！！！</div>
            
        </el-col>
        <el-col :span="5">
            <div class="airdetail-n">
                <div><img src="../img/认证 (1).png">
                    <span>航局认证</span>
                </div>
                <div>
                <img src="../img/电话.png">
                <span>安全认证</span>
                </div>
                <div>
                <img src="../img/保障.png">
                <span>质量保证</span>
                </div>
            </div>
            <div class="airdetail-p">
               热线电话：123456
            </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
import topnav from '@/components/topnav.vue'
import filghtitem from '../components/filghtitem.vue'
import request from '../api/request'
export default {
    name:'airdetail',
    components:{
      filghtitem,topnav
    },
    data(){
        return{
        airports: [],
        airport:'',
        airplaces: [],
        airsize: [{value:'大',label:'大'},{value:'中',label:'中'},{value:'小',label:'小'}],
        time:'',
        airplace:'',
        airmsg:[],
        starttime:'',
        time:{
          start:'',
          step:'00:15',
          end:''
        },
        filghts:[],
        airtime:[]

        }
        
    },
    methods:{
      //获取机票列表
      getdata(){
        this.airplace=''
        this.airport=''
        this.starttime=''
        const {city1,city2,time}=this.$route.query
      // time.split('')
      request({
         url:`/searchair.php?city1=${city1}&city2=${city2}`,
      }).then(res=>{
        this.airmsg=res.data
        // let re=/\d{2}\:\d{2}\:\d{2}/
        for(let i=0;i<res.data.length;i++){
          this.airports.push({value:res.data[i].astartplace,label:res.data[i].astartplace})
          this.airplaces.push({value:res.data[i].aair,label:res.data[i].aair})
        }
        //去重
        let set1=new Set(this.airports.map(item=>JSON.stringify(item)))
        this.airports=Array.from(set1).map(item=>JSON.parse(item))

        let set2=new Set(this.airplaces.map(item=>JSON.stringify(item)))
        this.airplaces=Array.from(set2).map(item=>JSON.parse(item))

        this.time.start=res.data[0].astarttime.slice(0,5)
        this.time.end=res.data[res.data.length-1].astarttime.slice(0,5)
        this.airmsg=res.data
        this.currentairmsg=res.data
        // console.log(res.data)
      })

      },

      seachdata(){
        request({
         url:`/searchmyair.php?time=${this.starttime}&airport=${this.airport}&airplace=${this.airplace}`,
      }).then(res=>{
        // console.log(res.data)
        if(res.data.message=='fail'){
          this.airmsg=[]

        }else{
          this.airmsg=res.data
        }
        
      })
      
      },
      gettime(){
        let date=new Date()
        let obj={'1':31,'3':31,'4':30,'5':31,'6':30,'7':31,'8':31,'9':30,'10':31,'11':30,'12':31}
        if(Number(date.getFullYear())%4==0){
          obj['2']=29
        }else{
          obj['2']=28
        }
        let mouth=date.getMonth()+1
        let day=date.getDay()
        if(mouth<10){
          mouth='0'+mouth
        }
        if(day<10){
          day='0'+day
        }
        this.airtime.push({
            label:mouth+'-'+day,
            value:mouth+'-'+day
           })
        // console.log(mouth)
        // console.log(day)
        for(let i=0;i<4;i++){
           if(day==obj.mouth){
            mouth=Number(mouth)+1
            day=Number(day)+1
           }else{
            day=Number(day)+1
           }
           if(Number(mouth)<10){
            mouth='0'+Number(mouth)
           }
          if(day<10){
             day='0'+day
          }
           this.airtime.push({
            label:mouth+'-'+day,
            value:mouth+'-'+day
           })
        }
        
        console.log(this.airtime)
      }

    },
    created(){
      this.getdata()
      this.gettime()
      this.time=this.$route.query.time.slice(5)
    }

}
</script>

<style>
.airdetail-item .money{
  color:red;
  font-weight: 600;
}
.airdetail-item{
  padding: 20px;
  margin-top:5px;
  border:1px solid lightgray
}
.airdetail-item:hover{
  box-shadow:2px 2px;
}
.airdetail-p{
    background-color: lightgray;
    padding: 5px 20px;
}
.airdetail-n img{
    width: 40px;
    height: 40px;
    display: block;
    margin-left:50% ;
    transform: translateX(-20px);
}
.airdetail-n{
    border: 1px solid lightgray;
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    padding: 10px;
}
.airdetail{
    padding-top: 10px;
}
.airdetail .el-select{
    width: 20%; 
}
.airdetail-title{
    height: 50px;
    background-color: lightgrey;
    margin-top:10px ;
    line-height: 50px;
}
.airdetail-but,.airdetail-search{
  margin-top:10px ;
}
</style>