<template>
    <div class="content">
      <div>
        <span style="margin-left:25% ;">—————&nbsp;&nbsp;</span>
        <span style="font-size: 40px;font-family:StKaiti;">{{ title }}</span>
        <span>&nbsp;&nbsp;—————</span>
        <span style="margin-left:20% ;"><router-link :to="rpath">查看更多<img src="../img/按钮-箭头向右.png" class="p"></router-link></span>
        </div>
        <br>
        <div style="display: flex;justify-content: space-between;flex-wrap: wrap;">
        <div class="content-item" v-for="item in itemlist" :key="item.id"  @click="hotaldetail(item.id)">
           <img :src="require('../img/'+item.pic)"> 
           <div class="hotalitem">
              <span style="font-size: 25px;font-weight: 700;">{{item.name}}</span><br>
              <span style="color:#ffa300;font-size:20px;">￥{{item.price}}</span><br><br><br>
              <span class="hother">{{item.other}}</span>
           </div>
        </div> 
        </div>
     </div>
  </template>
  
  <script>
  import request from "../api/request"
  export default {
    name:"hotal",
    props:['table','rpath','ipath','title'],
    data(){
      return {
        itemlist:[]
      }
    },
    methods:{
     hotaldetail(id){
        this.$router.push({
            path:`/${this.ipath}/${id}`
        })
     },
  
  
    },
    created(){
      request({
           url:`/gethomeitem.php?table=${this.table}`,
        }).then(res=>{
         console.log(res.data)
           this.hotalList=res.data;
           res.data.map(item=>{
            this.itemlist.push({
               id:item.hotalid||item.pid||item.gid,
               pic:item.hotalphoto||item.placephoto||item.gphoto,
               name:item.hotalname||item.placename||item.gname,
               price:item.hotalprice||item.placeprice||item.gprice,
               other:item.hotalother||item.placeother||item.gother
            })
           })
         //   console.log(this.itemlist)
        })
        
      
    }
  
  }
  </script>
  
  <style>
     .hotalitem .hother{
        display: none;
  
     }
     .hotalitem{
        position: absolute;
        top:60%;
        width: 100%;
        height: 40%;
        padding: 30px 30px;
        color: white;
        background-color: rgb(0,0,0,0.5);
        transition: height 0.8s,top 0.8s;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        cursor: pointer;
     }
     .hotalitem:hover{
      height:100%;
      top:0;
      border-radius: 10px;
     }
     .hotalitem:hover .hother{
      display: block;
     }
  </style>