<template>
  <div>
     <topnav/>
     <el-row>
        <el-col :span="12" :offset="6" >
            <div class="airordermsg-item">
                <h3 style="margin-bottom: 10px;display: inline;">应付款：</h3>
                <span style="color: red;font-weight: 600;">￥{{ air.price*person.length}}</span>
            </div>
           <div class="airordermsg-item">
            <p style="font-size: 30px;font-weight: 600;">{{ air.astartp }} - {{ air.aendp }}</p>
             <p>{{ air.astarttime }}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ air.astartplace }}</p>
             <div class="airordermsg-main" >
                   <p>{{ air.aair }} {{ air.aid }} </p>
             </div>
             <p>{{ air.aendtime }}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ air.aendplace }}</p>
             
           </div>
           <div class="airordermsg-item">
                <h3 style="margin-bottom: 10px;">用户信息</h3>
                <p v-for="item,index in person" :key="index">{{ item.name }}&nbsp;&nbsp;&nbsp;&nbsp;{{ item.id }}&nbsp;&nbsp;&nbsp;&nbsp;{{ item.sit }}</p>
            </div>
            <p class="airordermsg-bu">
                <el-button size="mini" @click="cancel">取消</el-button>
                <el-button type="primary" size="mini" @click="getorder">支付</el-button></p>
        </el-col>
        
     </el-row>
  </div>
</template>

<script>
import request from '../api/request'
import topnav from '@/components/topnav.vue';
export default {
    name:'airordermsg',
    components:{topnav},
    data(){
        return{
           air:{},
           person:this.$route.query.person,
        }
    },
    created(){
        console.log(this.$route.query.person)
        this.getairmsg()
    },
    methods:{
        cancel(){
          this.$router.back();
        },
        getairmsg(){
            request({
         url:`/getairmsg.php?id=${this.$route.query.id}`,
      }).then(res=>{
        this.air=res.data[0]
    //    console.log(res.data)
      },err=>{
        console.log(err)
      })
        },
        getorder(){
            let sit=''
            let name=''
            let id=''
            this.person.map(item=>{
                sit=sit+item.sit+';'
                name=name+item.name+';'
                id=id+item.id+';'
            })
            // console.log(sit,name,id)
            let d=new Date()
            let currenttime=d.getFullYear()+'-'+this.$route.query.time
           request({
              url:`/addorder.php?type=air&price=${this.air.price}
              &uid=${sessionStorage.getItem('username')}&id=${this.air.id}&sit=${sit}&uname=${name}&personid=${id}&time=${currenttime}`,
           }).then(res=>{
               if(res.data.messgae=='success'){
                this.$message({
                   message: '下单成功！',
                   type: 'success'
                 });
                 this.$router.push({
                    path:'/usermsg/airorder'
                 })
               }
           },err=>{
             console.log(err)
           })

        }
    }

}
</script>

<style>
.airordermsg-item{
    border: 1px solid lightgray;
    padding: 25px;
}
.airordermsg-item p{
    margin-bottom:10px ;
}
.airordermsg-main{
    border-left: 1px solid lightgrey;
    height:100px;
    margin-left:30px ;
    margin-bottom: 10px;
    padding:10px 15px ;
}
.airordermsg-bu{
    text-align: end;
    margin-top: 10px;
}
</style>