<template>
  <div>
     <myroder :type="type" :getdata="airdata"/>
  </div>
</template>

<script>
import myroder from '@/components/myroder.vue'
import request from '../api/request'
export default {
    name:'airorder',
    components:{
        myroder
    },
    data(){
        return{
            airdata:[],
            type:'air',
            buttonbefore:['退改签'],
            buttonafter:['评价','删除订单']
        }

    },
    methods:{
        getdata(){
            request({
            url:`/getorders.php?type=${this.type}&uid=${sessionStorage.getItem('username')}`,
          }).then(res=>{
           console.log(res.data)
           res.data.map(item=>{
            this.airdata.push({
                oid:item.oid,
                oname:item.place,
                ostatus:item.ostatus,
                oother:[`${item.astartp}-${item.aendp}`,`${item.astarttime}-${item.aendtime}`,`${item.aair} ${item.astartplace}`,'实付 ￥'+item.oprice],
                buttonbefore:this.buttonbefore,
                buttonafter:this.buttonafter
            })
           })
      })

        }
    },
    created(){
        this.getdata()
    }

}
</script>

