<template>
  <div class="myorder">
    <div class="myorder-nav">
        <el-button type="primary" size="mini">管理</el-button>
        <el-button type="danger" size="mini">批量删除</el-button>
    </div>
     <div v-for="item in getdata" :key="item.oid" class="myorder-item">
         <p @click="changepage(item.ooid)">{{ item.oname }}&nbsp;><span>{{ item.ostatus }}</span></p>
         <br>
         <el-row>
             <el-col :span="4" >
                <div v-if="item.ophoto!=undefined">
                <img :src="require('../img/'+item.ophoto)" >
                </div>
            </el-col>
            <el-col :span="17" :offset="1" class="myorder-detail">
                <p v-for="(i,index) in item.oother" :key="index"> {{ i }}</p>
            </el-col>
         </el-row>
         <br>
         <el-row>
            <el-col :span="17" :offset="7" style="position: relative;height:30px;">
                <div style="position: absolute;right: 10px;">
                <el-button v-for="(i,index) in item.buttonbefore" :key="index" size="mini" class="myorder-buu"> 
                    {{ i }}
                </el-button>
                </div>
            </el-col>
         </el-row>
    </div>
  </div>
</template>

<script>
export default {
    name:'myorder',
    props:['type','getdata'],
    methods:{
      changepage(id){
        if(this.type=='hotal'){
            this.$router.push({
              path:`/detailhotal/${id}`
            })
        }else if(this.type=='place'){
          this.$router.push({
              path:`/detailplace/${id}`
            })

        }
      }
    }

}
</script>

<style>
  .myorder{
    margin: 20px;
  }
  .myorder .myorder-item{
    border:1px solid lightgray;
    padding: 20px;
    border-radius: 10px;
    margin-bottom:10px ;
  }
  .myorder-item img{
    width: 100px;
    height: 100px;
    border-radius: 10px;

  }
  .myorder-item p{
    position: relative;
  }
  .myorder-item p span{
    position: absolute;
    right:10px;
  }
  .myorder-detail {
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    height: 100px;
  }
  .myorder-nav{
    margin-bottom:10px ;
  }

</style>