<template>
<div class="placemsg">
  <topnav/>
  <div class="placemsg-input">
  <el-input
    placeholder="请输入内容"
    prefix-icon="el-icon-search"
    v-model="input2" style="width: 400px;">
  </el-input>&nbsp;&nbsp;&nbsp;
  <el-button type="primary" size="small" @click="myplace">搜 索</el-button>
  </div>
  <div class="placemsg-content">
  <el-row>
    <el-col :span="5" :offset="2" style="border:1px solid lightgrey">
          <div class="hotplace">
            <h3>热门景点</h3><br>
            <div>
              <el-button v-for="item,index in hotmsg" :key="index" @click="showmsg(item.pid)">{{ item.placename }}</el-button>
            </div>
          </div>
    </el-col>
    <el-col :span="15" style="display: flex;flex-wrap: wrap;">
        <div class="placemsg-item" v-for="(item,index) in placemsg" :key="index">
           <img :src="require('../img/'+item.placephoto)" style="width: 100%;height: 200px;">
           <span style="font-size: 20px;font-weight: 600;margin:5px 10px;">{{ item.placename }}</span><br>
           <span style="color: red;font-weight: 600;margin:5px 10px;">￥{{ item.placeprice }}</span>
           <el-button type="primary" size="mini" style="margin-bottom:10px;margin-left: 50%;transform: translateX(-40px);width: 80px;" @click="showmsg(item.pid)">查看详情</el-button>
        </div>
    </el-col>
  </el-row>

   <pagination :pageNo="pagemsg.pageNo" :pageSize='pagemsg.pageSize' :total="pagemsg.total" 
     :continus="pagemsg.continus" @getcurrentpage="getcurrentpage"/>
  </div>
</div>  
</template>

<script>
import topnav from '@/components/topnav.vue'
import request from '../api/request'
import pagination from '../components/pagination.vue'
export default {
  components: { pagination,topnav },
  name:'placemsg',
  
  data(){
    return{
      input2:'',
      placemsg:[],
      pagemsg:{
          pageNo:1,  //当前页
          pageSize:6,  //一页多少条数据
          total:0,   //一共多少页
          continus:5  //出现几个按钮

        },
      hotmsg:[]
    
    }
  },
  methods:{
    showmsg(pid){
      this.$router.push({
        name:'detailplace',
        params:{id:pid}
      })

    },
    myplace(){
      request({
         url:`/mysearch.php?message=${this.input2}`,
      }).then(res=>{
        console.log(res.data)
        this.placemsg=res.data
      })
    },
    getcurrentpage(page){
      this.pagemsg.pageNo=page
      console.log(page)
      this.changepage()
      window.scrollTo(0, 0);

    },
    changepage(){
      request({
         url:`/search.php?currentpage=${this.pagemsg.pageNo}&onerows=${this.pagemsg.pageSize}&table=places`,
      }).then(res=>{
        console.log(res.data)
        this.pagemsg.total=res.data[res.data.length-1]
        res.data.pop();
        this.placemsg=res.data
      })

    },
    gethotplace(){
      request({
         url:`/gethotplace.php`,
      }).then(res=>{
        console.log('hot',res.data)
        this.hotmsg=res.data
        
      })

    }
  },
  mounted(){
    // console.log(this.$route)
  },
  created(){
    this.gethotplace()

    window.scrollTo(0,0)
    if(this.$route.query.city!=undefined){
      request({
        url:`/homesearch.php?city=${this.$route.query.city}&id=${this.$route.query.id}&currentpage=${this.pagemsg.pageNo}&onerows=${this.pagemsg.pageSize}&table=places`
      }).then(res=>{
        console.log(res.data)
        for(let i=0;i<res.data.length-1;i++){
          res.data[i].hotalscore=parseInt(res.data[i].hotalscore);
        }
        this.pagemsg.total=res.data[res.data.length-1]
        res.data.pop();
        this.placemsg=res.data

      })

    }else{
      this.changepage()
    }

      
  }
}
</script>

<style>
.placemsg-input{
  margin-top: 10px;
  text-align: center;
}
.placemsg-content{
  margin-top:10px ;
}
.placemsg-item{
  width: 200px;
  margin-top:10px ;
  border:3px solid white;
  border-radius: 15px;
  overflow: hidden;
  margin-left:20px ;
}
.placemsg-item:hover{
  border:3px solid #3fc1ff;
}
.placemsg-item span{
  display: inline-block;
}
.hotplace h3{
  padding: 10px;
  text-align: center;
}
.hotplace .el-button{
  margin-bottom:20px ;
}
.hotplace div{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}


</style>