<template>
  <div class="content">
    <div>
      <span style="margin-left:25% ;">—————&nbsp;&nbsp;</span>
      <span style="font-size: 40px;font-family:StKaiti;">酒店民宿</span>
      <span>&nbsp;&nbsp;—————</span>
      <span style="margin-left:20% ;"><router-link to="/hotalmsg">查看更多<img src="../img/按钮-箭头向右.png" class="p"></router-link></span>
      </div>
      <br>
      <div style="display: flex;justify-content: space-between;flex-wrap: wrap;">
      <div class="content-item" v-for="item in hotalList" :key="item.hotalid"  @click="hotaldetail(item.hotalid)">
         <img :src="require('../img/'+item.hotalphoto)"> 
         <div class="hotalitem">
            <span style="font-size: 25px;font-weight: 700;">{{item.hotalname}}</span><br>
            <span style="color:#ffa300;font-size:20px;">￥{{item.hotalprice}}</span><br><br><br>
            <span class="hother">{{item.hotalother}}</span>
         </div>
      </div> 
      </div>
   </div>
</template>

<script>
import request from "../api/request"
export default {
  name:"hotal",
  data(){
    return {
      hotalList:[]
    }
  },
  methods:{
   hotaldetail(id){
      this.$router.push({
         name:'detailhotal',
         params:{
            id:id
         }
      })
   },


  },
  created(){
    request({
         url:"/gethotallist.php",
      }).then(res=>{
         this.hotalList=res.data;
         // console.log(res.data)
      })
    
  }

}
</script>

<style>
   .hotalitem .hother{
      display: none;

   }
   .hotalitem{
      position: absolute;
      top:60%;
      width: 100%;
      height: 40%;
      padding: 30px 30px;
      color: white;
      background-color: rgb(0,0,0,0.5);
      transition: height 0.8s,top 0.8s;
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
      cursor: pointer;
   }
   .hotalitem:hover{
    height:100%;
    top:0;
    border-radius: 10px;
   }
   .hotalitem:hover .hother{
    display: block;
   }
</style>