<template>
  <div class="login2">
    <input type="text" placeholder="手机号/邮箱" ><br>
    <span ></span><br>
    <input type="text" placeholder="验证码" style="width:50%">&nbsp;&nbsp;
    <button class="getnum" @click="changebut()" :disabled="btdisable"><span>{{notice}}</span>
      <span v-show="flag">{{ time }}s</span></button><br>
    <span ></span><br>
    <input type="password" style="margin-top: 15px;" placeholder="设置密码"><br>
    <span></span><br>
    <button style="margin-top: 15px;" class="bu"><router-link to="/login/login1" style="color:white">注册</router-link></button>
</div>
</template>

<script>
export default {
   name:'login2',
   data(){
    return {
      notice:'获取验证码',
      time:5,
      flag:false,
      btdisable:false

    }
   },
   methods:{
    changebut(e){
       this.notice='重新发送';
       this.flag=true;
       this.btdisable=true;
       let id=setInterval(()=>{
           if(this.time!=0){
            this.time=this.time-1;
            console.log(11);
           }else{
              // this.notice='获取验证码';
              this.flag=false;
              this.btdisable=false;
           }
       },1000)

       if(this.time==0){
           clearInterval(id);
           this.time=5;
       }
    }
   }
}
</script>

<style>
.login2{
  margin-top: 15px;
}
.login2 .getnum{
  background-color: #ffa38c;
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
    color: white;
}
.login2 .bu{
    background-color: #ffa38c;
    border: none;
    padding: 10px 20px;
    width: 77%;
    margin-top:35px;
    border-radius: 5px;
   }
  .login2 input{
    outline-style: none;
      margin-top: 20px;
      padding: 8px 10px;
      width: 77%;
  }
</style>